import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'house-financing-details',
  templateUrl: './house-financing-details.component.html',
  styleUrls: ['./house-financing-details.component.scss']
})
export class HouseFinancingDetailsComponent {
  @Input() details;
  @Input() detailsType;
  custmerName;
  @Output() houseFinancingDetailsEvent = new EventEmitter();
  constructor() {

  }
  openModalPayment() {
    this.houseFinancingDetailsEvent.emit({ openCollectionModal: this.details})
  }
}
