import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
import { HttpService } from '../../../../../../services/http.service';
import { OrderCollectionType } from '../../../../../../shared/enums/enum';
import { InvoiceResponseModalComponent } from '../invoice-response-modal/invoice-response-modal.component';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'customer-payment-table',
  templateUrl: './customer-payment-table.component.html',
  styleUrls: ['./customer-payment-table.component.scss']
})
export class CustomerPaymentTableComponent {
  @Input() collects = [];
  @Output() eventData = new EventEmitter();
  sort: number = 1
  constructor(
    private modalService: NgbModal,
    public _spinner: NgxSpinnerService,
    public _httpService: HttpService,
  ) {
  }
  ngOnInit() {

  }
  generateInvoiceOrReceipt(orderType, from = '', row: any, event) {
    if (event) {
      event.stopPropagation();
    }
    this._httpService._spinnerService.show();
    let formData = new FormData();
    formData.append('OrderNo', row?.orderId);
    formData.append('OrderCollectionType', orderType);
    let URL = this._httpService.apiUrl.Regenerate.RegenerateOrigionalReceiptCustomer;
    this._httpService.post(URL, formData).subscribe({
      next: response => {
        if (response.isSuccess) {
          const modalRef = this.modalService.open(InvoiceResponseModalComponent, { centered: true });
          modalRef.componentInstance.data = {
            body: `Order ${from} generated successfully!`,
            otherData: { invoice: response.data, invoiceType: from, orderType }
          };
        }
      },
      error: err => {
        this.responseModal('error', err?.error?.errors);
      },
    }).add(() => this._httpService._spinnerService.hide());
  }
  responseModal(type, message) {
    const ref = this.modalService.open(ModalMessageComponent);
    ref.componentInstance.type = type;
    ref.componentInstance.dismissOnlyActive = true;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }

  handleRowClick(event, item) {
    let id = event.target.id;
    if (id === 'viewReceipt') {
      this.eventData.emit({ invoices: item?.invoices?.clientCopy })
    }
    else {
      this.eventData.emit({ customerPaymentDetails: item })

    }
    console.log(event.target.id);
    console.log(event.currentTarget);
  }
  sortByReceipt() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.eventData.emit({ sort: this.sort })
  }
  sortByCustomer() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.eventData.emit({ sort: this.sort })
  }
  sortByCategory() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.eventData.emit({ sort: this.sort })
  }
  sortByDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.eventData.emit({ sort: this.sort })
  }
  sortByAmount() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.eventData.emit({ sort: this.sort })
  }
}
