import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';
import { SharedService } from '../../../../../services/shared.service';
import { SubjectService } from '../../../../../services/subject.service';
import { ModalNeedmodifyComponent } from '../components/modal-needmodify/modal-needmodify.component';
import { ModalPayDoneComponent } from '../modal-payment/modal-pay-done/modal-pay-done.component';
import { PaymentModalComponent } from '../payment-modal/payment-modal.component';
import { CollectService } from '../services/collect.service';

@Component({
  selector: 'app-collect-details',
  templateUrl: './collect-details.component.html',
  styleUrls: ['./collect-details.component.scss'],
})
export class CollectDetailsComponent implements OnInit {
  items = [1, 2];
  detailsType: string;
  colType: number;
  detailsId: any;
  details: any;
  custmerName: string;
  subscription : Subscription;
  @ViewChild('ModalPayDoneComponent') ModalPayDoneComponent: ModalPayDoneComponent;
  constructor(
    private collectService: CollectService,
    private router: Router,
    private route: ActivatedRoute,
    public sharedService: SharedService,
    private modalService: NgbModal,
    private _subjectService: SubjectService,
  ) { }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
  ngOnInit(): void {
    this.route.params.subscribe(x => {
      this.detailsId = x['id'];
      this.detailsType = x['type']
      this.getDetails();
    })
    this.subscription = this._subjectService.isCollectionPaymentDone.subscribe(response => {
      if (response) {
        this.getDetails();
      }
    })
  }
  getDetails() {
    if (this.detailsId && this.detailsType == 'service') {
      this.GetServicesSalesOrderDetails();
      this.colType = 7001;
    } else if (this.detailsId && this.detailsType == 'payment') {
      this.GetDirectPaymentSalesOrdersDetails();
      this.colType = 7002;
    } else if (this.detailsId && this.detailsType == 'spare') {
      this.GetSparePartsSalesOrdersDetails();
      this.colType = 7003;
    }
    else if (this.detailsId && this.detailsType == 'undertaking') {
      this.getBanksUnderTakingSalesOrdersDetails();
      this.colType = 7006;
    }
    else if (this.detailsId && this.detailsType == 'housefinancing') {
      this.getInHouseFinancingSalesOrdersDetails();
      this.colType = 7007;
    }
  }
  openPaymentModal(event) {
    if (event?.openCollectionModal) {
      this.openModalPayment();
    }
  }
  getInHouseFinancingSalesOrdersDetails() {
    this.collectService.getInHouseFinancingSalesOrdersDetails(this.detailsId).subscribe((response: any) => {
      if (response) {
        this.details = response.data;
        console.log(this.details);
        const customername = this.details.customer.customerName.split(' ');
        let firstChar = customername[0];
        let secChar = customername[1];
        this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
      }
    });
  }
  GetServicesSalesOrderDetails() {
    return this.collectService
      .GetServicesSalesOrderDetails(this.detailsId)
      .subscribe((response: any) => {
        if (response) {
          this.details = response.data;
          console.log(this.details);
          const customername = this.details.customer.customerName.split(' ');
          let firstChar = customername[0];
          let secChar = customername[1];
          this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
        }
      });
  }

  GetDirectPaymentSalesOrdersDetails() {
    return this.collectService
      .GetDirectPaymentSalesOrdersDetails(this.detailsId)
      .subscribe((response: any) => {
        if (response) {
          this.details = response.data;
          console.log(this.details);
          const customername = this.details?.customer.customerName.split(' ');
          let firstChar = customername[0];
          let secChar = customername[1];
          this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
        }
      });
  }

  GetSparePartsSalesOrdersDetails() {
    return this.collectService
      .GetSparePartsSalesOrdersDetails(this.detailsId)
      .subscribe((response: any) => {
        if (response) {
          this.details = response.data;
          this.details = {
            ...this.details,
            invoiceType: this.details?.order?.invoiceType,
            sparePartsSalesOrderOpportunityNo: this.details?.order?.sparePartsSalesOrderOpportunityNo,
            grandAmount: this.details?.order?.grandAmount
          }
          console.log(this.details);
          const customername = this.details?.customer.customerName.split(' ');
          let firstChar = customername[0];
          let secChar = customername[1];
          this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
        }
      });
  }
  getBanksUnderTakingSalesOrdersDetails() {
    this.collectService.getBanksUnderTakingSalesOrdersDetails(this.detailsId).subscribe((response: any) => {
      if (response) {
        this.details = response.data;
        console.log(this.details);
        const customername = this.details?.customer.customerName.split(' ');
        let firstChar = customername[0];
        let secChar = customername[1];
        this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
      }
    });
  }

  openModalPayment() {
    //const modalRef = this.modalService.open(ModalPaymentComponent, {size: 'lg',});
    const modalRef = this.modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      if (this.colType == 7001 || this.colType == 7002 || this.colType == 7003) {
        if (this.colType == 7001)
          this._subjectService.collectedOrder.next({ orderId: this.details?.order?.servicesSalesOrderNo });
        else if (this.colType == 7002)
          this._subjectService.collectedOrder.next({ orderId: this.details?.order?.directPaymentSalesOrderOpportunityNo });
        else if (this.colType == 7003)
          this._subjectService.collectedOrder.next({ orderId: this.details?.order?.sparePartsSalesOrderOpportunityNo });
      }
    });
    if (this.colType == 7002)
      this.details = { ...this.details, minAmount: this.details?.order?.minAmount }
    modalRef.componentInstance.detailsItem = this.details;
    modalRef.componentInstance.collectionType = this.colType;
  }

  NeedModify() {
    const modalRef = this.modalService.open(ModalNeedmodifyComponent);
    modalRef.componentInstance.SalesOrderId = this.detailsId;
    modalRef.componentInstance.OrderType = this.details.orderTypeId;
    modalRef.componentInstance.action = 'modify';
    if (this.detailsId && this.detailsType == 'service') {
      modalRef.componentInstance.userType = 'service advisor';
    } else if (this.detailsId && this.detailsType == 'payment') {
      modalRef.componentInstance.userType = 'Sales Consultant';
    } else if (this.detailsId && this.detailsType == 'spare') {
      modalRef.componentInstance.userType = 'Sales Consultant';
    }
  }
  cancelOrder() {
    const modalRef = this.modalService.open(ModalNeedmodifyComponent);
    modalRef.componentInstance.SalesOrderId = this.detailsId;
    modalRef.componentInstance.OrderType = this.details.orderTypeId;
    modalRef.componentInstance.action = 'cancel';
  }
}
