import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SharedService } from '../../../../../../services/shared.service';
import { CollectService } from '../../services/collect.service';

@Component({
  selector: 'app-modal-needmodify',
  templateUrl: './modal-needmodify.component.html',
  styleUrls: ['./modal-needmodify.component.scss'],
})
export class ModalNeedmodifyComponent {
  @Input() SalesOrderId: any;
  @Input() OrderType: any;
  @Input() userType: string;
  @Input() action: string;

  message: string = '';
  messageError: string;

  notes: string;
  invalidInput = true;
  isPending = false;
  constructor(
    private collectService: CollectService,
    public sharedService: SharedService,
    private modalService: NgbModal,
    public activeModal: NgbActiveModal,

    private router: Router
  ) { }

  ngOnInit(): void { }
  handleNotesInput(event) {
    let value = event.target.value;
    this.invalidInput = value ? false : true;
  }
  NeedModify() {
    this.isPending = true;
    const formData = new FormData();
    formData.append('SalesOrderId', this.SalesOrderId);
    formData.append('OrderType', this.OrderType);
    formData.append('NeedModifyNote', this.notes);

    console.log(formData);
    this.collectService
      .NeedModify(formData)
      .subscribe((response: any) => {
        console.log(response);
        if (response.isSuccess) {
          this.isPending = false;
          this.message = 'Modify request successfully sent';
          setTimeout(() => {
            this.modalService.dismissAll();
            this.router.navigateByUrl('/collect');
          }, 4000);
        } else {
          console.log('error', response);
          this.isPending = false;
          this.messageError = response.Errors[0]?.ErrorMessageEn;
          setTimeout(() => {
            this.modalService.dismissAll();
            this.router.navigateByUrl('/collect');
          }, 4000);
        }
      });
  }
  cancelOrder() {
    this.isPending = true;
    const formData = new FormData();
    formData.append('OrderId', this.SalesOrderId);
    formData.append('OrderType', this.OrderType);
    formData.append('CancelNote', this.notes);
    this.collectService.cancelOrder(formData).subscribe((response: any) => {
      if (response.isSuccess) {
        this.isPending = false;
        this.message = 'Order canceled';
        setTimeout(() => {
          this.modalService.dismissAll();
          this.router.navigateByUrl('/collect');
        }, 4000);
      } else {
        console.log('error', response);
        this.isPending = false;
        this.messageError = response.Errors[0]?.ErrorMessageEn;
        setTimeout(() => {
          this.modalService.dismissAll();
          this.router.navigateByUrl('/collect');
        }, 4000);
      }
    }, error => {
      this.isPending = false;
      this.messageError = error.error.errors[0]?.ErrorMessageEn;

    });
  }
}
