import { Component, EventEmitter, Input, Output, SimpleChange } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';
import { HeaderService } from '../../../../../../services/header.service';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'bank-under-taking',
  templateUrl: './bank-under-taking.component.html',
  styleUrls: ['./bank-under-taking.component.scss']
})
export class BankUnderTakingComponent {

  @Input() collects = [];
  @Input() collectOrderType;
  @Input() data;
  @Output() bankUnderTakingEventData = new EventEmitter();
  sort: number = 1;

  checkedItems = [];
  multiCollectData;
  customerId = 0;
  isFilteredByCustomer = false;
  isMultiSelect = false;
  collectionOrderNum = 7006;
  constructor(private _commonService: CommonService, private _headerService: HeaderService, private _modalService: NgbModal) {

  }
  ngOnChanges(changes: SimpleChange) {
    if (changes['data']?.currentValue) {
      this.resetMultiSelectProps();
    }
    if (changes['collects']?.currentValue) {
      this.checkedItems = this.collects.filter(x => x.checked);
    }
  }
  ngOnInit() {
  }
  bankUndertakingOrderDetails(item) {
    this.bankUnderTakingEventData.emit({ viewDetails: item })
    //  localStorage.setItem('collectionOrderNum', '7006');
    //  this._commonService.NavigateToRoute('/collect/details/undertaking/' + item.banksUnderTakingSalesOrderOpportunityNo);
    //  this._headerService.setTitle('Collect > Bank Undertaking Orders List');
  }
  checkAll(event) {
    let check = event.target.checked;
    if (!check) {
      this.bankUnderTakingEventData.emit({ resetCustomerId: true })
      this.isFilteredByCustomer = false;
    }
    else {
      this.bankUnderTakingEventData.emit({ isMultiSelect: true });
    }
  }
  handleCheckBoxChange() {
    this.checkedItems = this.collects.filter((x: any) => x.checked);
    let grandAmount = 0;
    let remainingAmount = 0;
    let orderNo = [];
    this.checkedItems.forEach(x => {
      grandAmount += x['grandAmount'];
      remainingAmount += x['remainingAmount'];
      orderNo.push(x['banksUnderTakingSalesOrderOpportunityNo']);
    })
    this.multiCollectData = {
      grandAmount,
      remainingAmount: +(remainingAmount.toFixed(3)),
      orderCollectionType: this.collectionOrderNum,
      customerName: this.checkedItems[0]?.customer['customerName'],
      isMultiCollect: true,
      orderNo: orderNo.toString(),
    };
    console.log(this.checkedItems);
    console.log(this.multiCollectData);
  }
  filterCustomerById(item) {
    this.isFilteredByCustomer = true;
    this.customerId = item?.customer?.customerId;
    this.bankUnderTakingEventData.emit({ customerId: this.customerId, pageNo: 0 })
  }
  resetMultiSelectProps() {
    this.multiCollectData = null;
    this.isFilteredByCustomer = false;
    this.checkedItems = [];
    this.isMultiSelect = false;
    this.customerId = 0;
  }
  openMultiCollectionModal() {
    this.handleCheckBoxChange();
    this.bankUnderTakingEventData.emit({ multiCollectData: this.multiCollectData })
  }
  collectOrder(item) {
    this.bankUnderTakingEventData.emit({ item, collectPayment: true })
  }
  downloadInvoices(invoices) {
    if (invoices) {
      this.bankUnderTakingEventData.emit({ invoices })
    }
    else {
      const modalRef = this._modalService.open(ModalMessageComponent);
      modalRef.componentInstance.type = 'error';
      modalRef.componentInstance.messageError = [{ errorMessageEn: 'No invoices to download' }];
    }
  }
  sortByCard() {
    if (this.isMultiSelect)
      return
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.bankUnderTakingEventData.emit({ sort: this.sort })
  }
  sortByCustomer() {
    if (this.isMultiSelect)
      return
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.bankUnderTakingEventData.emit({ sort: this.sort })
  }
  sortByConsultant() {
    if (this.isMultiSelect)
      return
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.bankUnderTakingEventData.emit({ sort: this.sort })
  }
  sortByAmount() {
    if (this.isMultiSelect)
      return
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.bankUnderTakingEventData.emit({ sort: this.sort })
  }
  sortByStatus() {
    if (this.isMultiSelect)
      return
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.bankUnderTakingEventData.emit({ sort: this.sort })
  }
}
