import { DatePipe } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CollectService } from '../../../services/collect.service';
import { ModalMessageComponent } from '../../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'app-pn-receiving',
  templateUrl: './pn-receiving.component.html',
  styleUrls: ['./pn-receiving.component.scss']
})
export class PnReceivingComponent {
  @Input() details: any;
  @Output() eventData = new EventEmitter();
  notesList = [];
  checkedItems = [];
  constructor(
    private _datePipe: DatePipe,
    public _activeModal: NgbActiveModal,
    public _collectService: CollectService,
    public _modalService: NgbModal,
  ) { }
  ngOnInit() {
    console.log(this.details);
    this.notesList = this.details?.selectedItems.map(x => { return { ...x, checked: false } });
  }
  handleCheckBoxChange() {
    this.checkedItems = this.notesList.filter((x: any) => x.checked);
  }
  submit() {
    let data;
    data = this.notesList.filter(item => item.checked).map(x => {
      return { pnBookNoteId: x?.pnBookNoteId, receivedByCustomer: true }
    });
    if (data.length == 0) {
      this.responseModal('error', [{ ErrorMessageEn: 'Please select pns' }]);
      return;
    }
    this._collectService.receivePNByCustomer(data).subscribe(response => {
      if (response.isSuccess) {
        this.eventData.emit(true);
      }
    }, error => {
      this.responseModal('error', error?.error?.errors);
    })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    ref.componentInstance.dismissOnlyActive = true;
    ref.componentInstance.type = type;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }
}
