import { DatePipe } from '@angular/common';
import { Component, Input, Optional } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../services/common.service';
import { CollectService } from '../services/collect.service';

@Component({
  selector: 'app-pn-order-details',
  templateUrl: './pn-order-details.component.html',
  styleUrls: ['./pn-order-details.component.scss']
})
export class PnOrderDetailsComponent {
  @Input() data;
  notesList = [];
  pnOrderDetails;
  totalRecordCount = 0;
  sort = 1
  pageNo = 1;
  currentDate = '';
  constructor(
    public _modalService: NgbModal,
    public _activeModal: NgbActiveModal,
    public _collectService: CollectService,
    public _commonService: CommonService,
    public _datePipe: DatePipe,
  ) { }
  ngOnInit() {
    //this.currentDate = this._datePipe.transform(new Date(), 'MMM d, y');
    this.getPNOrdersBooks();
    this.getPNOrdersBooksPNS();
  }
  highlightDate(date) {
    let currentDate = new Date(this._datePipe.transform(new Date(), 'MMM d, y'));
    let pnDate = new Date(date);
    if (currentDate > pnDate)
      return 'primary-bg'
    else if (currentDate < pnDate)
      return 'blue-bg'
    else
      return 'green-bg'
  }
  getPNOrdersBooksPNS() {
    let params = `?PNOrderId=${this.data?.pnOrderId}&isDue=${false}&PageNo=${this.pageNo - 1}&PageSize=6`;
    this._collectService.getPNOrdersBooksPNS(params).subscribe((response: any) => {
      this.notesList = response.data;
      this.totalRecordCount = response.totalRecordCount;
    })
  }
  getPNOrdersBooks() {
    let params = `?PNOrderId=${this.data?.pnOrderId}`;
    this._collectService.getPNOrdersBooks(params).subscribe((response: any) => {
      this.pnOrderDetails = response.data[0];
      console.log(response);
    })
  }
  pageChange(page) {
    this.pageNo = page
    this.getPNOrdersBooksPNS();
  }
  collect() {
    this._activeModal.close();
    this._commonService.NavigateToRoute(`/collect/collect-pn-order/${this.data?.pnOrderId}`);
  }
}
