import { DatePipe } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CollectService } from '../../../services/collect.service';
import { ModalMessageComponent } from '../../../../../../../shared/components/modal-message/modal-message.component';
import { HttpService } from '../../../../../../../services/http.service';
import { Subject, catchError, forkJoin, of, takeUntil } from 'rxjs';

@Component({
  selector: 'app-add-follow-up',
  templateUrl: './add-follow-up.component.html',
  styleUrls: ['./add-follow-up.component.scss'],
  providers: [DatePipe],
})
export class AddFollowUpComponent {
  destroy$ = new Subject<void>;
  @Input() details: any;
  @Output() eventData = new EventEmitter();
  followUpForm: FormGroup;
  hardCopyLocationList = [];
  isFormChanged = false;
  constructor(
    private _datePipe: DatePipe,
    public _activeModal: NgbActiveModal,
    public _httpService: HttpService,
    public _modalService: NgbModal,
    public fb: FormBuilder,
  ) { }
  ngOnInit() {
    this.initForm();
    this.getLookups();
  }
  initForm() {
    this.followUpForm = this.fb.group({
      followUPNoteText: [''],
      promiseDate: [''],
      legalActionInitiated: [false],
      receivedByCustomer: [false,],
      hardCopyLocationID: [this.details?.pnHardCopyAddressID?.lookupID],
    }, { validators: this.atLeastOneRequired() });
    this.followUpForm.valueChanges.pipe(takeUntil(this.destroy$)).subscribe(x => {
      this.isFormChanged = true;
    })
    this.followUpForm.get('promiseDate').valueChanges.pipe(takeUntil(this.destroy$)).subscribe(value => {
      const formattedDate = this._httpService._helperService.formatDateToISO(value);
      this.followUpForm.get('promiseDate').setValue(new Date(formattedDate), { emitEvent: false });
    })
  }
  atLeastOneRequired(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const followUPNoteText = control.get('followUPNoteText')?.value;
      const promiseDate = control.get('promiseDate')?.value;
      const hardCopyLocationID = control.get('hardCopyLocationID')?.value;
      const receivedByCustomer = control.get('receivedByCustomer')?.value;
      const legalActionInitiated = control.get('legalActionInitiated')?.value;
      return (receivedByCustomer || legalActionInitiated || hardCopyLocationID || followUPNoteText || promiseDate) ? null : { atLeastOneRequired: true };
    };
  }
  getLookups() {
    this._httpService._spinnerService.show();
    const hardCopy = this._httpService.get(`${this._httpService.apiUrl.Lookup.GetLookups}?lookupTypeId=81&pageSize=1000&active=1001`, 'master').pipe(catchError(error => of(error)));
    forkJoin([hardCopy]).subscribe(response => {
      this.hardCopyLocationList = response[0].data;

    }).add(() => this._httpService._spinnerService.hide())
  }

  saveFollowUp() {
    let values = this.followUpForm.value;
    let obj = {
      noteId: this.details?.pnBookNoteId,
      followUPNoteText: values?.followUPNoteText,
      ...(values?.promiseDate && { promiseDate: values?.promiseDate }),
      legalActionInitiated: values?.legalActionInitiated,
      receivedByCustomer: values?.receivedByCustomer,
      hardCopyLocationID: values?.hardCopyLocationID,
    }
    if (this.followUpForm.invalid) {
      this.followUpForm.markAllAsTouched();
      return;
    }
    this._httpService._spinnerService.show();
    this._httpService.post(`${this._httpService.apiUrl.PNsFollowUpControler.AddPNNoteFollowUp}`, [obj], 'cm').subscribe({
      next: response => {
        if (response.isSuccess) {
          this.eventData.emit(true);
          this._activeModal.close();
          this.responseModal('success', 'Follow up added successfully!');
        }
      },
      error: err => {
        this.responseModal('error', err?.error?.errors);
      }
    }).add(() => { this._httpService._spinnerService.hide() })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    ref.componentInstance.type = type;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
