  getTransferFeesOrders() {
    let params = `?status=${this.orderStatus || ''}&customer=${this.searchText}&TransferOrderId=${this.bankCardNo}&OpportunityNo=${this.vin}&FromDate=${this.dateObj?.fromDate}&ToDate=${this.dateObj?.toDate}&sort=${this.sort}`;
    if (this.customerId > 0)
      //IN CASE OF MULTI COLLECT 
      params += `&customerId=${this.customerId}&PageNo=0&PageSize=1000`
    else
      params += `&PageNo=${this.pageNo - 1}&PageSize=6`
    this.collectService.getTransferFeesOrders(params).subscribe((response: any) => {
      if (response?.isSuccess) {
        this.collects = [...response.data];
        this.collects = this.collects.map(x => { return { ...x, checked: true } });
        this.totalAllRecordsCount = response.info?.totalRecordsCount;
        this.totalRecordsCount = response.info?.totalRecordsCount;
        this.totalRecords = response.info?.totalRecordsCount;
        console.log(response);
      }
    })
  }
  getPNOrdersBooks() {
    let params = `?status=${this.orderStatus || ''}&Customer=${this.searchText}&PnNo=${this.vin}&PnBookNo=${this.amount}&FromDate=${this.dateObj?.fromDate}&ToDate=${this.dateObj?.toDate}&sort=${this.sort}&PageNo=${this.pageNo - 1}&PageSize=6`;
    this.collectService.getPNOrdersBooks(params).subscribe((response: any) => {
      this.collects = [...response.data];
      this.collects = this.collects.map(x => { return { ...x, checked: true } });
      this.totalAllRecordsCount = response.info?.totalRecordsCount;
      this.totalRecordsCount = response.info?.totalRecordsCount;
      this.totalRecords = response.info?.totalRecordsCount;
      console.log(response);
    })
  }
