  customerPaymentEvent(event) {
    if (event?.invoices) {
      this.downloadInvoices(event.invoices)
    }
    if (event?.customerPaymentDetails) {
      this.openDetailsModal(event?.customerPaymentDetails)
    }
    if (event?.sort) {
      this.sort = event?.sort
      this.loadDataWithCheck();
    }
  }
  houseFinanceEventData(event) {
    if (event?.isMultiSelect) {
      this.isMultiSelect = true;
    }
    if (event?.sort) {
      this.sort = event?.sort
      this.loadDataWithCheck();
    }
    if (event?.collectPayment) {
      this.openModalPaymentTypes(event?.item)
    }
    if (event?.invoices) {
      this.downloadInvoices(event?.invoices?.clientCopy)
    }
    if (event?.customerId > 0) {
      this.isFilteredByCustomer = true;
      this.customerId = event?.customerId;
      this.pageNo = event?.pageNo;
      this.getInHouceFinancingSalesOrders();
    }
    if (event?.resetCustomerId) {
      this.customerId = 0;
      this.pageNo = 1;
      this.getInHouceFinancingSalesOrders();
      this.isFilteredByCustomer = false;
      this.isMultiSelect = false;
    }
    if (event?.multiCollectData?.remainingAmount > 0) {
      this.openModalPaymentTypes(event?.multiCollectData);
    }
    if (event?.viewDetails) {
      this.ViewCollectDetails(event?.viewDetails);
    }
  }
