import { Component, EventEmitter, Input, Output, SimpleChange } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { PnOrderDetailsComponent } from '../../pn-order-details/pn-order-details.component';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'promissory-notes',
  templateUrl: './promissory-notes.component.html',
  styleUrls: ['./promissory-notes.component.scss']
})
export class PromissoryNotesComponent {
  isMultiSelect = false;
  sort = 1;
  @Input() collects;
  @Input() data;
  @Output() pnNotesEventData = new EventEmitter();
  constructor(
    private _modalService: NgbModal,
  ) { }
  ngOnInit() {
  }
  viewDetails(event, id) {
    let target = (event.target as HTMLElement).tagName.toLowerCase();
    if (target == 'button')
      return;
    else {
      //this._commonService.NavigateToRoute(`/collect/collect-pn-order/${id}`);
      let obj = { pnOrderId: id };
      const modalRef = this._modalService.open(PnOrderDetailsComponent, { size: 'xl' });
      modalRef.componentInstance.data = obj;
    }
  }
  collectOrder(item) {
    this.pnNotesEventData.emit({ viewDetails: item })
  }
  downloadInvoices(invoices) {
    if (invoices) {
      this.pnNotesEventData.emit({ invoices })
    }
    else {
      const modalRef = this._modalService.open(ModalMessageComponent);
      modalRef.componentInstance.type = 'error';
      modalRef.componentInstance.messageError = [{ errorMessageEn: 'No invoices to download' }];
    }
  }
  //sorting function

  sortByPnOrderNo() {
    if (this.isMultiSelect)
      return
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByPnBook() {
    if (this.isMultiSelect)
      return
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByCustomer() {
    if (this.isMultiSelect)
      return
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByDueDate() {
    if (this.isMultiSelect)
      return
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByLowAmount() {
    if (this.isMultiSelect)
      return
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByHighAmount() {
    if (this.isMultiSelect)
      return
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }
  sortByStatus() {
    if (this.isMultiSelect)
      return
    if (this.sort == 15) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 14 ? 15 : 14;
    }
    this.pnNotesEventData.emit({ sort: this.sort })
  }

}
