import { Component, EventEmitter, HostListener, Input, Optional, Output } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';
import { CollectService } from '../../services/collect.service';

@Component({
  selector: 'paymentExpressForm',
  templateUrl: './payment-express-form.component.html',
  styleUrls: ['./payment-express-form.component.scss']
})
export class PaymentExpressFormComponent {
  banks: [];
  @Input() terminals = [];
  expressForm: FormGroup;
  @Input() recentFormValues
  @Input() isPending: boolean;
  @Input() otherData;
  @Output() amountEvent: EventEmitter<any> = new EventEmitter<any>();
  @Output() formValueEvent: EventEmitter<any> = new EventEmitter<any>();
  @HostListener('document:keydown.tab', ['$event']) onKeydownHandler() {
    this.handleTabKeyDown();
  }
  constructor(private collectService: CollectService, private fb: FormBuilder, @Optional()  public _activeModal: NgbActiveModal, private _commonService: CommonService) {
  }
  ngOnInit() {
    //this.getBanks();
    this.initForm();
    this.expressForm.statusChanges.subscribe(x => {
      this.expressForm.markAllAsTouched();
      this.formValueEvent.emit({ values: this.expressForm.value, touched: x == 'VALID' ? false : true })
    })
  }
  closeModal() {
    if (this._activeModal)
      this._activeModal.close()
    else
      this._commonService.NavigateToRoute(`/${this.otherData?.redirectRoute}`)
  }
  initForm() {
    this.expressForm = this.fb.group({
      cardArray: this.fb.array([this.cardFormControls()])
    })
    if (this.recentFormValues?.express?.cardArray?.length) {
      let values = this.recentFormValues['express'].cardArray
      let formArray = (this.expressForm.get('cardArray') as FormArray);
      formArray.removeAt(0);
      for (let item of values) {
        formArray.push(this.cardFormControls(item))
      }
    }
  }
  handleAmountFocus() {
    this.expressForm.markAllAsTouched();
  }
  ngOnDestroy() {
    if (this.recentFormValues?.express?.cardArray[0]?.cardCollectionJod > 0)
      this.formValueEvent.emit({ values: this.expressForm.value, touched: false })
  }
  cardFormControls(item?) {
    if (item) {
      return this.fb.group({
        isChecked: [item?.checked],
        cardCollectionJod: [item?.cardCollectionJod, [Validators.min(0.0000000000000001), Validators.required]],
        machineReceiptNumber: [item?.machineReceiptNumber, [Validators.required, Validators.pattern('^[0-9]*$')]],
        cardTerminalProviderId: [item?.cardTerminalProviderId, [Validators.required, Validators.min(1)]],
        cardPayerName: [item?.cardPayerName, [Validators.required]],
        cardLast4Digits: [item?.cardLast4Digits, [Validators.required, Validators.pattern('[0-9]{4}$')]],
        paymentCardTypeId: [13003],
      });
    }
    else {
      return this.fb.group({
        isChecked: [false],
        cardCollectionJod: ['', [Validators.min(0.0000000000000001), Validators.required]],
        machineReceiptNumber: ['', [Validators.required, Validators.pattern('^[0-9]*$')]],
        cardTerminalProviderId: [null, [Validators.required]],
        cardPayerName: ['', [Validators.required]],
        cardLast4Digits: ['', [Validators.required, Validators.pattern('[0-9]{4}$')]],
        paymentCardTypeId: [13003],
      });
    }
  }
  removeItem(i) {
    this.cardArrControls.removeAt(i);
    this.sendAmount();
  }
  get cardArrControls() {
    return this.expressForm ? this.expressForm.get('cardArray') as FormArray : null;
  }
  handleTabKeyDown() {
    if (this.expressForm.invalid) {
      return;
    }
    if (this.otherData['amountExceeded']) {
      return;
    }
    if (!this.otherData?.isMultiCollect)
      this.addNewRow();
  }
  addNewRow() {
    this.cardArrControls.push(this.cardFormControls());
    this.expressForm.markAsUntouched();

  }
  isSameCustomer(event, index) {
    let formGroup = this.cardArrControls.at(index) as FormGroup;
    if (event.target.checked) {
      let customerName = localStorage.getItem('pmcn');
      formGroup.controls['cardPayerName'].setValue(customerName);
    }
    else {
      formGroup.controls['cardPayerName'].setValue('');
    }
  }
  handleSubmit() {
    if (this.expressForm.invalid) {
      this.expressForm.markAllAsTouched();
    }
    else {
      this.formValueEvent.emit({ values: this.expressForm.value, submit: true })
    }
  }
  get f() {
    return this.expressForm.controls;
  }
  sendAmount() {
    this.calculateAmount();
  }
  calculateAmount() {
    let amount = 0;
    for (let item of this.cardArrControls.controls) {
      let formGroup = item as FormGroup;
      amount += +(formGroup.controls['cardCollectionJod'].value);
    }
    this.amountEvent.emit({ amount });
  }
  getBanks() {
    this.collectService.getLookupsById(11).subscribe(
      (response: any) => {
        this.banks = response.data.map((x) => ({
          id: x.id,
          name: x.name[0].lookupName,
        }));
      }
    );
  }
  resetForm() {
    this.expressForm.reset();
    this.expressForm.markAsUntouched();
    //this.expressForm.controls['amexDate'].setValue((new Date()).toISOString().substring(0, 10));
    this.formValueEvent.emit({ values: this.expressForm.value, touched: false, reset: true });
    this.amountEvent.emit({ amount: 0 });
  }
}

