import { DatePipe } from '@angular/common';
import { Component, Optional, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { PaymentModalComponent } from '../payment-modal/payment-modal.component';
import { CollectService } from '../services/collect.service';
import { AddFollowUpComponent } from './components/add-follow-up/add-follow-up.component';
import { PnReceivingComponent } from './components/pn-receiving/pn-receiving.component';
import { SubjectService } from '../../../../../services/subject.service';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';


@Component({
  selector: 'app-collect-pn-order',
  templateUrl: './collect-pn-order.component.html',
  styleUrls: ['./collect-pn-order.component.scss']
})
export class CollectPnOrderComponent {
  notesList = [];
  pnOrderDetails;
  totalRecordCount = 0;
  pageNo = 1;
  details;
  collectionType = 7008;

  payFor = 'due';
  amountToPay: boolean;
  pnOrderId = 0;
  checkedItems = [];
  selectedPnAmount = 0;
  currentDate = '';
  selectAll = false;
  formGroup: FormGroup;

  earlySettlementRequest;
  earlySettlemenNotesList = [];
  earlySettleTotalRecordCount = 0;
  earlySettlePageNo = 1;
  requestId = 0;
  constructor(
    public _modalService: NgbModal,
    public _collectService: CollectService,
    public _activeRoute: ActivatedRoute,
    public _datePipe: DatePipe,
    public fb: FormBuilder,
    public _subjectService: SubjectService,
    @Optional() public _activeModal: NgbActiveModal
  ) { }
  ngOnInit() {
    this._activeRoute.params.subscribe(params => {
      this.pnOrderId = params['id'];
      this.getPNOrdersBooksPNS();
      this.getPNOrdersBookDetails();
    })
    this.formGroup = this.fb.group({
      amountToPay: [{ value: '', disabled: true }, [Validators.required, Validators.min(0.0000000000000001)]]
    })
    this._subjectService.paymentDoneModalClosed.subscribe((response: any) => {
      if (response.closed) {
        if (this.checkedItems.length) {
          let pnReceiving = [...this.checkedItems];
          setTimeout(() => {
            this.pnReceiving(pnReceiving);
          }, 1000)
          this.checkedItems = [];
        }
        else {
          this._modalService.dismissAll();
        }
      }
    })
  }
  addFollowUp(item) {
    const modalRef = this._modalService.open(AddFollowUpComponent, { size: 'lg', centered: true })
    modalRef.componentInstance.details = { ...item, customerName: this.pnOrderDetails?.customerName, phone: this.pnOrderDetails?.mobile };
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        this.getPNOrdersBooksPNS();
        this.getPNOrdersBookDetails();
      }
    })
  }
  highlightDate(date) {
    let currentDate = new Date(this._datePipe.transform(new Date(), 'MMM d, y'));
    let pnDate = new Date(date);
    if (currentDate > pnDate)
      return 'primary-bg'
    else if (currentDate < pnDate)
      return 'blue-bg'
    else
      return 'green-bg'
  }
  handleAmountToPay(event) {
    let checked = event.target.checked;
    if (checked) {
      this.formGroup.get('amountToPay').enable()
      this.notesList = this.notesList.map(x => { return { ...x, checked: false } });
      this.selectedPnAmount = 0;
      this.selectAll = false;
      this.checkedItems = [];
    }
    else {
      this.formGroup.get('amountToPay').disable();
    }
  }
  getPNOrdersBooksPNS() {
    let params = `?PNOrderId=${this.pnOrderId}&isDue=${true}&PageNo=${this.pageNo - 1}&PageSize=1000`;
    this._collectService.getPNOrdersBooksPNS(params).subscribe((response: any) => {
      this.notesList = response.data.map(x => { return { ...x, checked: false } });
      this.totalRecordCount = response.totalRecordCount;
    })
  }
  getPNOrdersBookDetails() {
    let param = `?pnNo=${this.pnOrderId}`
    this._collectService.getPNOrdersBooks(param).subscribe((response: any) => {
      console.log(response);
      this.pnOrderDetails = response.data[0];
    })
  }
  handleCheckBoxChange() {
    this.calcValue();
  }
  calcValue() {
    this.checkedItems = this.notesList.filter((x: any) => x.checked);
    this.selectedPnAmount = 0;
    this.checkedItems.forEach(x => {
      this.selectedPnAmount += x.remainingAmount;
    })
    this.formGroup.get('amountToPay').setValue(this.selectedPnAmount);
    this.amountToPay = false;
    console.log(this.checkedItems);
  }
  checkAll(event) {
    let check = event.target.checked;
    if (check) this.notesList = this.notesList.map(x => { return { ...x, checked: true } });
    else this.notesList = this.notesList.map(x => { return { ...x, checked: false } });
    this.calcValue();
  }
  handleInputPayFor(event, type) {
    this.payFor = type;
    if (this.payFor == 'early') {
      this.getOrdersEarlySettlement();
    }
  }
  hanldleInput(event) {
    let x = event.target.value;
    this.notesList = this.notesList.map(x => { return { ...x, checked: false } });
    this.selectedPnAmount = 0;
    this.selectAll = false;
    this.checkedItems = [];
    if (this.pnOrderDetails.remainingPNAmount < +x) this.formGroup.controls['amountToPay'].setErrors({ 'amountExceeded': true });
  }
  getOrdersEarlySettlement() {
    let params = `?orderId=${this.pnOrderId}&pageNo=0&pageSize=${1000}`;
    this._collectService.getOrdersEarlySettlement(params).subscribe((response: any) => {
      console.log(response);
      this.earlySettlementRequest = response.data[0];
      if (this.earlySettlementRequest?.statusObj?.lookupId == 21018) {
        this.requestId = this.earlySettlementRequest?.earlySetlementRequestId;
        this.getOrdersEarlySettlementNotes()
      }
    })
  }
  getOrdersEarlySettlementNotes() {
    let params = `?earlySettlementRequestId=${this.requestId}&pageNo=0&pageSize=${6}`;
    this._collectService.getOrdersEarlySettlementNotes(params).subscribe((response: any) => {
      console.log(response);
      this.earlySettlemenNotesList = response.data;
    })
  }
  pageChange(page) {
    this.pageNo = page
    this.getPNOrdersBooksPNS();
    this.checkedItems = [];
    this.selectedPnAmount = 0;
    this.selectAll = false;
  }
  earlySettlePageChange(page) {
    this.earlySettlePageNo = page
    this.getOrdersEarlySettlementNotes();
  }
  collect() {
    if (this.payFor == 'early') {
      this.details = {
        pnOrderId: this.pnOrderId,
        customerName: this.pnOrderDetails?.customerName,
        grandAmount: this.earlySettlementRequest?.totalEarlySettlementAmount,
        remainingAmount: this.earlySettlementRequest?.totalEarlySettlementAmount,
      }
    }
    else {
      let amount = +this.formGroup.get('amountToPay').value;
      this.details = {
        pnOrderId: this.pnOrderId,
        customerName: this.pnOrderDetails?.customerName,
        grandAmount: amount,
        remainingAmount: amount,
        ...((this.checkedItems.length > 0 && !this.amountToPay) && { pnBookNotesIds: this.checkedItems.map(x => x.pnBookNoteId).toString() || '' })
      }
    }
    this.details = { ...this.details, earlysettlement: this.payFor == 'early' ? true : false };
    const modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
    modalRef.componentInstance.detailsItem = this.details;
    modalRef.componentInstance.collectionType = this.collectionType;
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      //let pnReceiving = [...this.checkedItems];
      //setTimeout(() => {
      //  this.pnReceiving(pnReceiving);
      //}, 1000)
      //this.checkedItems = [];
      this.selectedPnAmount = 0;
      this.selectAll = false;
      this.amountToPay = false;
      this.formGroup.get('amountToPay').setValue('');
      this.formGroup.get('amountToPay').disable();
      this.getPNOrdersBooksPNS();
    });
    localStorage.setItem('collectionOrderNum', this.collectionType.toString());
  }
  pnReceiving(selectedItems) {
    const modalRef = this._modalService.open(PnReceivingComponent, { size: 'lg', centered: true })
    modalRef.componentInstance.details = { selectedItems }
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        this._modalService.dismissAll();
        this.responseModal('success', `PN's received successfully!`);
      }
    })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    ref.componentInstance.dismissOnlyActive = true;
    ref.componentInstance.type = type;
    if (type == 'success')
      ref.componentInstance.message = message;
    else
      ref.componentInstance.messageError = message;
  }
}
