import { Component, Input } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';

@Component({
  selector: 'app-invoice-response-modal',
  templateUrl: './invoice-response-modal.component.html',
  styleUrls: ['./invoice-response-modal.component.scss']
})
export class InvoiceResponseModalComponent {
  @Input() data: any;
  constructor(
    public _acitveModal: NgbActiveModal,
    public _modalService: NgbModal,
    public _helperService: CommonService,
  ) { }
  viewInvoice() {
    if (this.data?.otherData?.invoice) {
      this._helperService.downloadURI(this.data?.otherData?.invoice)
    }
  }
}
