  chooseCategory(e) {
    console.log(e);
    console.log(e.taxPercent);

    this.FormRevenue.get('revenueCollection')
      .get('taxPercent')
      .patchValue(e.taxPercent);

    let taxAmount =
      (this.FormRevenue.get('revenueCollection').get('taxPercent').value *
        this.FormRevenue.get('revenueCollection').get('amount').value) /
      100;
    console.log(taxAmount);
    this.FormRevenue.get('revenueCollection').get('tax').patchValue(+(taxAmount).toFixed(3));

    this.FormRevenue.get('revenueCollection')
      .get('totalAmount')
      .patchValue(
        (+this.FormRevenue.get('revenueCollection').get('amount').value +
          +this.FormRevenue.get('revenueCollection').get('tax').value).toFixed(3)
      );

    this.calcRemainingAmount.emit(
      this.FormRevenue.get('revenueCollection').get('totalAmount').value
    );
  }

  calcTotalAmount(e) {
    let amount = +e.target.value;
    if (amount > 0) {
      this.FormRevenue.get('revenueCollection').get('amount').patchValue(+amount);

      let taxAmount =
        (this.FormRevenue.get('revenueCollection').get('taxPercent').value *
          amount) /
        100;
      console.log(taxAmount);
      this.FormRevenue.get('revenueCollection').get('tax').patchValue(taxAmount.toFixed(3));

      this.FormRevenue.get('revenueCollection')
        .get('totalAmount')
        .patchValue(
          (+this.FormRevenue.get('revenueCollection').get('amount').value +
            +this.FormRevenue.get('revenueCollection').get('tax').value).toFixed(3)
        );

      this.calcRemainingAmount.emit(
        this.FormRevenue.get('revenueCollection').get('totalAmount').value
      );

    }
  }
  clearCustomer(e) {
    this.FormRevenue.get('revenueCollection').get('mobile').patchValue('');
  }
  clearCategory(e) {
    this.FormRevenue.get('revenueCollection').get('taxPercent').patchValue(0);
  }
