          this.FormRevenue.get('revenueCollection')
            .get('taxPercent')
            .patchValue('');
          this.FormRevenue.get('revenueCollection')
            .get('totalAmount')
            .patchValue(this.orderDetails.totalAmount);
          this.FormRevenue.get('revenueCollection')
            .get('mobile')
            .patchValue(this.orderDetails.customer?.mobile);
          this.FormRevenue.get('revenueCollection')
            .get('costCenter')
            .patchValue(this.orderDetails.note);
          this.FormRevenue.get('revenueCollection')
            .get('note')
